.nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-bottom: .25rem;
  width: 98%;
  max-width: 1410px;
  margin-left: auto;
  margin-right: auto;
}

.nav_sound {
  font-family: 'Lato';
  margin: auto 0 2px 0;
  font-weight: bold;
}

.nav_monogram {
  margin-top: .5rem;
}

.nav_contact {
  font-family: 'Lato';
  margin: auto 0 2px 0;
  color: #67182e;
  font-weight: bold;
}

.nav_separator {
  background-color: black;
  height: 1px;
  width: 98%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1410px;
  margin-bottom: 5vh;
}

@media (min-width: 1400px) {
  .nav_separator {
    margin-bottom: 115px;
  }
}

.monogram {
  width: 44px;
  height: 20px;
}

.nav-contact-link {
  color: #67182e;
  text-decoration: none;
}
