.footer-container {
    height: 450px;
    // display: -webkit-box;
    // display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    align-items: center;
    // margin: 50px 15px 50px 15px;
    // position: relative;
    // width: 100v;
    max-width: 1440px;
    margin: 0 auto 50px auto;
    
}

.footer-wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    // bottom: 0;
    // left: 15vw;
    // right: 15vw;
    width: 95vw;
    height: 450px;
    margin: 0 auto 0 auto;
}

.footer-text-link {
// font-size: 30px;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
position:absolute;
}

.footer-text {

    font-family: 'Libre';
    font-size: 75px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.73;
    letter-spacing: -1.09px;
    text-align: center;
    color: white;
    margin: 0 0 0 0;
}

.footer-text-talk-link {
    font-family: Lato;
    font-size: 100px;
    font-weight: 900;
    letter-spacing: -1.45px;
    color: #67182e;
    margin: 0 0 0 0;
    text-decoration:underline;
    
}
.footer-text-talk-link:hover {
    color: black;
}
.footer-text-talk-link:hover {
    cursor:pointer;
}

.footer-tb {
    width: 100%;
    margin-top: 0 auto 0 auto;
    height: auto;
}

.footer-divider{
    // width:98vw;
    height:1px;
    background-color: black;
    margin-left: 15px;
    margin-right: 15px;
}
.footer-bottom-container {
    display: flex;
    justify-content: space-between;
    margin-left:15px;
    margin-right: 15px;
    // width: 97vw;
    height: 75px;
    align-items: center;
}

.footer-linkedin {
    font-family: Lato;
    font-size: 17px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 2px;
    color: #111;
}

.footer-arrmg {
    font-family: Lato;
  font-size: 17px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 2px;
  text-align: right;
  color: #111;
}